import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { PictProvider } from '../../providers/pict/pict';
import { SocialSharing } from '@ionic-native/social-sharing';
import { AlertController } from 'ionic-angular';
import { Instagram } from '@ionic-native/instagram';

/**
 * Generated class for the ProfilePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html',
})
export class ProfilePage {

  picture : any;
  message : string = null;
  file : string = null;
  link : string = null;
  subject : string = null;
  

  constructor(public navCtrl: NavController, public navParams: NavParams, private pictProvider:PictProvider, private socialSharing:SocialSharing, public alertCtrl: AlertController, public instagram:Instagram) {
  }

  loginWithFB() {
    this.navCtrl.push("FacebookPage");
  }

  doPrompt() {
    let prompt = this.alertCtrl.create({
      title: 'Share',
      message: "Enter the details of what you wish to share : ",
      inputs: [
        {
          name: 'message',
          placeholder: 'Enter the message :'
        },
        {
          name : 'subject',
          placeholder : 'Enter the subject : '
        },
        {
          name: 'file',
          placeholder: 'Enter the file :'
        },
        {
          name: 'link',
          placeholder: 'Enter the link :'
        },
      ],
      buttons: [
        {
          text: 'Cancel',
          handler: data => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Share',
          handler: data => {
            console.log(JSON.stringify(data));
            console.log(data.message,data.subject,data.file,data.link);
            this.message = data.message;
            this.subject = data.subject;
            this.file = data.file;
            this.link = data.link;
            this.shareMedia();
            console.log('Saved clicked');
          }
        }
      ]
    });
    prompt.present();
  }

    shareMedia()
    {
      this.socialSharing.share(this.message, this.subject, this.file, this.link)
      .then(()=>{
  
      }).catch(()=>{
  
      });
    }

    shareInsta()
    {
      this.instagram.share(this.picture, 'This was copied to my Clipboard')
      .then(() => console.log('Shared!'))
        .catch((error: any) => console.error(error));
    }

  openCamera()
  {
    this.pictProvider.openCamera().then((value)=>{
      this.picture=this.pictProvider.myPhoto;
    }).catch(err=>{
      console.log(err,"error in get picture from camera");
    })
    
  }

  openGallery()
  {
    this.pictProvider.openGallery().then((value)=>{
      this.picture=this.pictProvider.myPhoto;
  }).catch(err=>{
      console.log(err,"error in get picture from camera");
  })
  
  }

  cropPicture()
  {
    this.pictProvider.cropPicture().then((value)=>{
     this.picture=this.pictProvider.myPhoto;
    }).catch(err=>{
      console.log(err,"error in get picture from camera");
  })

  }

  locationDirect()
  {
    this.navCtrl.setRoot("LocationPage");
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ProfilePage');
  }
}



